<template>
  <v-layout justify-center row fill-height wrap>
    <v-flex xs12 md6>
      <v-text-field
      v-model="address"
      label="Address"
      required
      ></v-text-field>
    </v-flex>
    <v-flex xs12 md2>
      <v-btn @click="getData" dark color="info">GO!</v-btn>
    </v-flex>
    <v-flex xs8 class="amber lighten-4 pa-3 mt-3 mb-5" v-if="activated">
      <v-layout row wrap v-if="loading">
        <v-flex xs-12 class="text-xs-center">
          <v-progress-circular
            indeterminate
            color="red darken-4"
            :size="100"
            class="text-xs-center align-center d-inline-block"
            :width:="0"
          ></v-progress-circular>
        </v-flex>
      </v-layout>
      <h2 class="mb-3" v-if="lat">Latitude: {{lat}}</h2>
      <h2 v-if="long">Long: {{long}}</h2>
    </v-flex>
  </v-layout>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        address: '',
        addressURL: '',
        long: '',
        lat: '',
        loading: false,
        error: false,
        activated: false
      }
    },
    methods: {
      getData: function() {
        this.addressURL = this.address.replace(/\s/g , "+").replace(/,/g, '%2C')
        this.activated = true
        this.loading = true
        // console.log('https://open.mapquestapi.com/geocoding/v1/address?key=1uuhXqut4WAmpvTTdbvcxNGhTH9215Bi&outFormat=json&location=' + this.addressURL + '&thumbMaps=false')
        axios.get('https://www.mapquestapi.com/geocoding/v1/address?key=1uuhXqut4WAmpvTTdbvcxNGhTH9215Bi&outFormat=json&location=' + this.addressURL + '&thumbMaps=false').then(
          res => {
            console.log(res)
            this.lat = res.data.results[0].locations[0].latLng.lat
            this.long = res.data.results[0].locations[0].latLng.lng
            this.loading = false
          })
      }
    }
  }
</script>

